@import url(https://fonts.googleapis.com/css?family=Solway:400,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  box-shadow: none;
  border: none;
  font-family: 'Solway', serif; }

body {
  margin: 0;
