  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  background-color: #0F1A20; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.app {
  padding: 5vh 7.5vw; }
  .app > header {
    position: absolute;
    top: 0;
    right: 0; }
    .app > header h1 {
      color: #017E91;
      display: flex;
      align-items: center;
      padding: 20px; }
      .app > header h1:hover {
        color: #fff; }
      .app > header h1:before {
        content: '';
        width: 36px;
        height: 36px;
        margin-right: 10px;
        background-image: url(/CloudWIS/static/media/logo.466ae145.svg); }
      .app > header h1 a {
        color: inherit;
        text-decoration: none; }


.side-menu {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 200; }
  .side-menu main {
    display: flex;
    height: 100vh;
    width: 100vw; }

.menu-icon {
  cursor: pointer;
  fill: #017E91;
  width: 40px;
  border-radius: 50%;
  margin: 20px; }
  .menu-icon:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    fill: #ffffff; }
  .menu-icon:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }

.menu-panel {
  background-color: #0f1a20;
  color: #fff;
  width: 35vw;
  height: 100%;
  padding: 20px;
  position: relative; }

.darkscreen {
  background-color: #fff;
  opacity: 0.8;
  width: 100%;
  height: 100%; }

.back-icon {
  cursor: pointer;
  fill: #fff;
  height: 25px;
  margin: 20px;
  position: absolute;
  top: 0;
  right: 0; }
  .back-icon:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  .back-icon:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }

.city-search__form {
  display: flex;
  flex-direction: column; }
  .city-search__form label {
    font-size: 24px;
    padding-bottom: 5px;
    border-bottom: 2px solid #0D324D;
    margin-right: 80px; }
  .city-search__form input {
    border-radius: 10px;
    font-size: 18px;
    padding: 0.4em;
    padding-left: 20px;
    margin: 40px 0 20px; }

.button-wrapper {
  display: flex;
  justify-content: space-between; }
  .button-wrapper button {
    cursor: pointer;
    border-radius: 20px;
    font-size: 18px;
    font-weight: 700;
    padding: 0.4em 2em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; }
    .button-wrapper button:last-child {
      background-color: #144e79;
      color: #fff; }
    .button-wrapper button:first-child {
      background-color: #fff;
      color: #0D324D; }
    .button-wrapper button:hover {
      background-color: #185d8e;
      color: #fff; }
    .button-wrapper button:active {
      background-color: #092437; }

.history {
  margin-top: 40px; }
  .history h2 {
    font-size: 24px;
    font-weight: 400;
    padding-bottom: 5px;
    border-bottom: 2px solid #0D324D;
    margin-right: 80px; }
  .history ul {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0; }

.history-item {
  cursor: pointer;
  font-size: 18px;
  font-weight: 700;
  list-style-type: none;
  background-color: #fff;
  border: 2px solid #0D324D;
  border-radius: 20px;
  color: #0D324D;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 0.5em 2em; }
  .history-item:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  .history-item:active {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }

.weather-widget {
  display: flex;
  background-color: #020202;
  border-radius: 20px;
  width: 85vw;
  height: 90vh;
  overflow: hidden; }

.weather-image {
  background-color: #fff;
  background-image: url(/CloudWIS/static/media/bg.861450be.svg);
  background-position: center;
  background-size: cover;
  border-radius: 20px;
  width: 30vw;
  height: 100%;
  overflow: hidden; }

.rainy {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative; }

.rain {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2; }
  .rain.back-row {
    display: block;
    z-index: 1;
    bottom: 60px;
    opacity: 0.5; }

.drop {
  position: absolute;
  bottom: 100%;
  width: 15px;
  height: 120px;
  pointer-events: none;
  -webkit-animation: drop 0.5s linear infinite;
          animation: drop 0.5s linear infinite; }
  .drop div {
    width: 1px;
    height: 60%;
    margin-left: 7px;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 0.25)));
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.25));
    -webkit-animation: stem 0.5s linear infinite;
            animation: stem 0.5s linear infinite; }

@-webkit-keyframes drop {
  0% {
    -webkit-transform: translateY(0vh);
            transform: translateY(0vh); }
  100% {
    -webkit-transform: translateY(100vh);
            transform: translateY(100vh); } }

@keyframes drop {
  0% {
    -webkit-transform: translateY(0vh);
            transform: translateY(0vh); }
  100% {
    -webkit-transform: translateY(100vh);
            transform: translateY(100vh); } }

@-webkit-keyframes stem {
  0% {
    opacity: 1; }
  65% {
    opacity: 1; }
  75% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes stem {
  0% {
    opacity: 1; }
  65% {
    opacity: 1; }
  75% {
    opacity: 1; }
  100% {
    opacity: 0; } }

